import { useRouter } from 'next/router';
import { IdeationWorkbench } from '../../components/workbench/IdeationWorkbench';
import type { GetServerSidePropsContext } from 'next/types';

export async function getServerSideProps(context: GetServerSidePropsContext) {
  const { id } = context.params!;
  return {
    props: { id },
  };
}

export default function WorkbenchPage({ id }: { id: string }) {
  const router = useRouter();
  const { id: routerId } = router.query as { id: string };

  return (
    <div className="workbench-container">
      <IdeationWorkbench initialIdea={id ?? routerId} />

      <style jsx>{`
        .workbench-container {
          width: 100%;
          height: 100vh;
          display: flex;
          flex-direction: column;
        }
      `}</style>
    </div>
  );
}
